import React from 'react';
import Img from 'gatsby-image';

import { screen, colors } from '../constants';

export default function BlockLogos({ title, titlePosition = 'center', data }) {
  return (
    <section className="block-logos">
      <div className="app__pretitle block-logos__title">{title}</div>
      <div className="block-logos__content">
        {data.map(({ image, name, offsetTop = 0 }, index) => (
          <Img
            key={index}
            fluid={image}
            imgStyle={{ objectFit: 'contain' }}
            alt={name}
            className="app__image block-logos__image"
            style={{ top: offsetTop }}
          />
        ))}
      </div>
      <style jsx>{`
        .block-logos {
          margin: 24px 0 60px;
        }
        .block-logos__content {
          display: flex;
          justify-content: space-between;
          align-items: flex-end;
        }

        @media (max-width: ${screen.mobileMax}px) {
          .block-logos__content {
            align-items: center;
            flex-direction: column;
          }
        }

        .block-logos__title {
          font-size: 16px;
          font-weight: 500;
          line-height: 2.5;
          letter-spacing: 1.3px;
          text-align: ${titlePosition};
          margin-bottom: 35px;
          text-transform: uppercase;
          color: ${colors.title};
        }
      `}</style>
      <style jsx global>{`
        .block-logos__image {
          margin: 15px;
          flex: 1;
        }
        @media (max-width: ${screen.mobileMax}px) {
          .block-logos__image {
            min-width: 150px;
          }
        }
      `}</style>
    </section>
  );
}
